@charset "UTF-8";
/** Webflow start **/
.w-webflow-badge {
  display: none !important;
  visibility: hidden !important;
}

/** Webflow end **/
/** Slider start **/
.w-slider-nav-invert > div {
  background-color: #dfdfdf;
}
.w-slider-nav-invert > div.w-active {
  background-color: #f80;
}
.w-slider-dot {
  width: 1.25em;
  height: 1.25em;
}

/** Slider end **/
/** Navbar start **/
.navbar-main_link-underline {
  transform: translateX(-50%) scale3d(0.75, 1, 1) !important;
  transition: all 120ms ease;
}
.navbar-main_link:hover .navbar-main_link-underline {
  transform: translateX(-50%) scale3d(1.2, 1, 1) !important;
}

.navbar-main_link.w--current .navbar-main_link-underline,
.navbar-main_link:hover .navbar-main_link-underline {
  opacity: 100;
}

@media screen and (max-width: 767px) {
  .navbar-main_link-underline {
    display: none;
  }
}
/** Form start **/
.form_input:focus-visible {
  outline: none;
}

.form_richtext ul li {
  list-style-type: disc;
  list-style-position: inside;
}

